/* Reset some default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Set default font styles */
html {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  background-color: rgb(255, 255, 255);
  color: #000;
}

/* Define global styles */
body {
  min-height: 100vh;
  font-size: 1.5rem;
}

input, textarea, button, select {
  font: inherit;
  margin-bottom: 1em;
}

/* Header styles */
header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: rgb(199, 14, 107);
  color: whitesmoke;
  position: sticky;
  top: 0;
}

nav {
  display: flex;
  justify-content: flex-end;
}

nav ul {
  list-style-type: none;
}

nav ul li {
  display: inline-block;
  margin-right: 1rem;
}

nav a, nav a:visited {
  color: #fff;
  text-decoration: none;
}

nav a:hover, nav a:focus {
  text-decoration: underline;
}

/* Main content styles */
main {
  max-width: 700px;
  margin: auto;
}

section {
  margin-top: 1em;
}

article {
  margin: 0.5em;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 1em;
}

/* Typography styles */
h1 {
  font-size: 3.5rem;
}

h2 {
  margin-bottom: 1rem;
}

p {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  font-size: 1.2rem;
  margin: 0.5em 0;
}

/* Form styles */
form {
  display: flex;
  flex-direction: column;
}

textarea {
  height: 200px;
}

/* Post credit styles */
.postCredit {
  font-size: 1rem;
}

.postCredit a, .postCredit a:visited {
  margin-right: 0.5rem;
  color: black;
}

.postCredit a:hover, .postCredit a:focus {
  color: hsla(0, 0%, 0%, 0.75);
}

/* Excerpt styles */
.excerpt {
  font-style: italic;
}

/* Button styles */
.reactionButton {
  margin: 0 0.25em 0 0;
  background: transparent;
  border: none;
  color: #000;
  font-size: 1rem;
}

.deleteButton {
  background-color: palevioletred;
  color: white;
}
